.g-hd {
  position: fixed;
  width: 100%;
  height: 90px;
}
.g-hd .top-nav {
  margin: 0 auto;
  width: 1506px;
  min-width: 1200px;
  height: 100%;
}
.g-hd .top-nav .left-nav {
  height: 100%;
  display: inline-block;
}
.g-hd .top-nav .right-nav {
  margin-left: 190px;
  height: 100%;
  display: inline-block;
}
.g-hd .top-nav .right-nav li {
  margin-right: 25px;
  line-height: 90px;
  color: #727171;
  font-size: 20px;
}
.g-hd .top-nav .right-nav .wechat {
  position: relative;
  margin-right: 15px !important;
  margin-top: 34px;
  width: 28px;
  height: 22px;
  background-image: url('../../images/wechat.png');
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
}
.g-hd .top-nav .right-nav .wechat .pop {
  display: none;
  position: absolute;
  top: 35px;
  right: -55px;
  width: 140px;
  height: 140px;
}
.g-hd .top-nav .right-nav .weibo {
  margin-top: 34px;
  width: 25px;
  height: 22px;
  background-image: url('../../images/weibo.png');
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
}
.g-hd .top-nav .right-nav .tel {
  margin-right: 10px !important;
  margin-top: 28px;
  width: 34px;
  height: 34px;
  background-image: url('../../images/400.png');
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
}
.g-hd .top-nav .right-nav .tel-number {
  color: #fff;
}
.g-hd .top-nav li {
  font-size: 18px;
  float: left;
  list-style: none;
  margin-right: 45px;
  line-height: 90px;
}
.g-hd .top-nav .logo a {
  display: block;
  margin-top: 10px;
  background-image: url('../../images/logo_w.png');
  height: 72px;
  width: 153px;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
}
.g-hd.g-nav1 {
  background-color: #fff;
}
.g-hd.g-nav1 a {
  color: #727171;
}
.g-hd.g-nav1 .logo a {
  background-image: url('../../images/logo_p.png') !important;
}
.g-hd.g-nav1 .wechat {
  background-image: url('../../images/wechat_deep.png') !important;
}
.g-hd.g-nav1 .weibo {
  background-image: url('../../images/weibo_deep.png') !important;
}
.g-hd.g-nav1 .tel {
  background-image: url('../../images/400_deep.png') !important;
}
.g-hd.g-nav1 .tel-number {
  color: #727171 !important;
}
